// components/ChatScreen/index.jsx
import React, { useEffect, useState, useRef, useContext } from "react";
import { useRouter } from "next/router";
import { useAuthState } from "react-firebase-hooks/auth";
import { auth, db } from "../../firebase";
import {
  collection,
  query,
  where,
  orderBy,
  onSnapshot,
  addDoc,
  serverTimestamp,
  doc,
  updateDoc,
  getDocs,
} from "firebase/firestore";
import { useCollection } from "react-firebase-hooks/firestore";
import styled from "styled-components";
import { DarkModeContext } from "../DarkModeProvider";
import getRecipientEmail from "../../utils/getRecipientEmail";
import { MESSAGE_STATUS } from "./constants";
import ChatHeader from "./components/ChatHeader";

function ChatScreen({ chat, isMobile, onToggleSidebar }) {
  const [user] = useAuthState(auth);
  const [input, setInput] = useState("");
  const [messages, setMessages] = useState([]);
  const messagesEndRef = useRef(null);
  const router = useRouter();
  const { id: chatId } = router.query;

  // Get dark mode context
  const darkModeContext = useContext(DarkModeContext);
  const { darkMode } = darkModeContext || { darkMode: false };

  // Get recipient email
  const recipientEmail = chat?.users ? getRecipientEmail(chat.users, user) : null;

  // Check if self-chat
  const isSelfChat = recipientEmail === user?.email;

  console.log(`üîç [Chat: ${chatId}] Chat initialized:`, {
    currentUser: user?.email,
    recipient: recipientEmail,
    isSelfChat,
  });

  // Get recipient info
  const recipientQuery =
    recipientEmail && !isSelfChat
      ? query(collection(db, "users"), where("email", "==", recipientEmail))
      : null;
  const [recipientSnapshot] = useCollection(recipientQuery);
  const recipient = recipientSnapshot?.docs?.[0]?.data();

  // Load messages
  useEffect(() => {
    if (!chatId) return;

    const messagesRef = collection(db, "chats", chatId, "messages");
    const q = query(messagesRef, orderBy("timestamp", "asc"));

    const unsubscribe = onSnapshot(
      q,
      (snapshot) => {
        const msgs = snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setMessages(msgs);

        // Auto-scroll to bottom
        setTimeout(() => {
          messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
        }, 100);
      },
      (error) => {
        console.error(`‚ùå [Chat: ${chatId}] Error loading messages:`, error);
      }
    );

    return () => unsubscribe();
  }, [chatId]);

  // ‚úÖ Mark received messages as read when viewing chat
  useEffect(() => {
    if (!chatId || !user?.email || isSelfChat) return;

    let isActive = true;

    const markReceivedMessagesAsRead = async () => {
      try {
        const messagesRef = collection(db, "chats", chatId, "messages");
        const snapshot = await getDocs(messagesRef);

        if (snapshot.empty || !isActive) return;

        const updates = [];

        snapshot.docs.forEach((messageDoc) => {
          const data = messageDoc.data();

          // Only update messages FROM other users
          if (data.user === user.email) return;

          // Mark as read if not already
          if (
            data.status === MESSAGE_STATUS.SENT ||
            data.status === MESSAGE_STATUS.DELIVERED
          ) {
            updates.push(
              updateDoc(messageDoc.ref, {
                status: MESSAGE_STATUS.READ,
                readAt: serverTimestamp(),
              })
            );
          }
        });

        if (updates.length > 0 && isActive) {
          await Promise.all(updates);
          console.log(`‚úÖ [Chat: ${chatId}] Marked ${updates.length} messages as read`);
        }
      } catch (error) {
        console.error(`‚ùå [Chat: ${chatId}] Error marking as read:`, error);
      }
    };

    // Mark on load
    markReceivedMessagesAsRead();

    // Mark on window focus
    const handleFocus = () => {
      if (isActive) markReceivedMessagesAsRead();
    };

    window.addEventListener("focus", handleFocus);

    return () => {
      isActive = false;
      window.removeEventListener("focus", handleFocus);
    };
  }, [chatId, user, isSelfChat]);

  // ‚úÖ Send message with correct status
  const sendMessage = async (e) => {
    e.preventDefault();

    if (!input.trim() || !user || !chatId) return;

    const messageText = input.trim();
    setInput("");

    console.log(`üì§ [Chat: ${chatId}] Preparing to send message to ${recipientEmail}`);

    try {
      const messagesRef = collection(db, "chats", chatId, "messages");

      // ‚úÖ Self-chat = "read", Normal chat = "sent"
      const initialStatus = isSelfChat ? MESSAGE_STATUS.READ : MESSAGE_STATUS.SENT;

      const messageData = {
        message: messageText,
        user: user.email,
        timestamp: serverTimestamp(),
        status: initialStatus,
      };

      if (isSelfChat) {
        messageData.readAt = serverTimestamp();
      }

      console.log(`üì§ [Chat: ${chatId}] Sending message with status: ${initialStatus}`);

      await addDoc(messagesRef, messageData);

      // Update chat's lastMessage
      const chatRef = doc(db, "chats", chatId);
      await updateDoc(chatRef, {
        lastMessage: messageText,
        lastMessageTime: serverTimestamp(),
      });

      console.log(`‚úÖ [Chat: ${chatId}] Message sent successfully`);

      // Auto-scroll
      setTimeout(() => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
      }, 100);
    } catch (error) {
      console.error(`‚ùå [Chat: ${chatId}] Error sending message:`, error);
      setInput(messageText);
    }
  };

  if (!chat || !recipientEmail) {
    return (
      <Container darkMode={darkMode}>
        <p>Loading chat...</p>
      </Container>
    );
  }

  return (
    <Container darkMode={darkMode}>
      <ChatHeader
        recipientEmail={recipientEmail}
        recipient={recipient}
        isSelfChat={isSelfChat}
        isMobile={isMobile}
        onToggleSidebar={onToggleSidebar}
        darkMode={darkMode}
      />

      <MessagesContainer darkMode={darkMode}>
        {messages.map((msg) => (
          <Message
            key={msg.id}
            message={msg}
            isOwnMessage={msg.user === user?.email}
            darkMode={darkMode}
          />
        ))}
        <div ref={messagesEndRef} />
      </MessagesContainer>

      <InputContainer darkMode={darkMode} onSubmit={sendMessage}>
        <Input
          darkMode={darkMode}
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder={isSelfChat ? "Message yourself..." : "Type a message..."}
        />
        <SendButton type="submit" disabled={!input.trim()}>
          Send
        </SendButton>
      </InputContainer>
    </Container>
  );
}

export default ChatScreen;

// Styled Components
const Container = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: ${(props) => (props.darkMode ? "#0d1418" : "#e5ddd5")};
`;

const MessagesContainer = styled.div`
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background-color: ${(props) => (props.darkMode ? "#0d1418" : "#e5ddd5")};

  ::-webkit-scrollbar {
    width: 8px;
  }

  ::-webkit-scrollbar-track {
    background: ${(props) => (props.darkMode ? "#1e1e1e" : "#f1f1f1")};
  }

  ::-webkit-scrollbar-thumb {
    background: ${(props) => (props.darkMode ? "#555" : "#888")};
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: ${(props) => (props.darkMode ? "#777" : "#555")};
  }
`;

const InputContainer = styled.form`
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: ${(props) => (props.darkMode ? "#1e1e1e" : "#f0f0f0")};
  border-top: 1px solid ${(props) => (props.darkMode ? "#333" : "#ddd")};
`;

const Input = styled.input`
  flex: 1;
  padding: 12px 15px;
  border: none;
  border-radius: 25px;
  outline: none;
  background-color: ${(props) => (props.darkMode ? "#2a2a2a" : "white")};
  color: ${(props) => (props.darkMode ? "#e0e0e0" : "#000")};
  font-size: 15px;

  ::placeholder {
    color: ${(props) => (props.darkMode ? "#888" : "#999")};
  }
`;

const SendButton = styled.button`
  margin-left: 10px;
  padding: 10px 20px;
  background-color: #25d366;
  color: white;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.2s;

  &:hover:not(:disabled) {
    background-color: #20ba5a;
  }

  &:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }

  &:active:not(:disabled) {
    transform: scale(0.98);
  }
`;